padding-left: 25%;
text-align: center;
}

.header{
  text-align: left;
  color: black;
  padding-top: 1em;
  padding-bottom: 0.2em;
}

.app {
  background: -webkit-linear-gradient(85deg, #80a8f1 0%, #e5f5a0 100%);
  background: linear-gradient(5deg, #80a8f1 0%, #e5f5a0 100%);
  width: 55%;
  min-height: 200px;
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: space-evenly;
}

.score-section {
  display: flex;
  font-size: 24px;
  align-items: center;
}

/* QUESTION/TIMER/LEFT SECTION */
.question-section {
  width: 100%;
  position: relative;
}

.question-count {
  margin-bottom: 20px;
}

.question-count span {
  font-size: 28px;
}

.question-text {
  margin-bottom: 12px;
}


/* ANSWERS/RIGHT SECTION */
.answer-section {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

button {
  width: 100%;
  font-size: 16px;
  color: #3d0a0a;
  background: -webkit-gradient( linear, left top, left bottom, from(#cab096), to(#e4e0e2));
  background: -webkit-linear-gradient( #cab096 0%, #e4e0e2 100%);
  background: linear-gradient( #cab096 0%, #e4e0e2 100%);
  border-radius: 15px;
  display: flex;
  padding: 5px;
  margin: 5px;
  justify-content: flex-start;
  align-items: center;
  border: 3px solid #234668;
  cursor: pointer;
}


button:hover {
  background-color: #1b295c;
}

button:focus {
  outline: none;
}

button svg {
  margin-right: 5px;
}

